<template>
  <div :class="`${prefixCls}__detail`">
    <div class="flex flex-row items-center">
      <div :class="`${prefixCls}__detail--name`" @click.stop="handleShowTokenList">
        <span class="uppercase select-none">
          {{ selectedTokenInfo && `${selectedTokenInfo?.coin}/${selectedTokenInfo?.market}` }}
        </span>
        <img
          :class="{ trangleUp: showTokenList }"
          :src="require('@/assets/img/trangle_down.png')"
          alt=""
        />
        <div class="symbol-list anim-scale-in" v-show="showTokenList">
          <span
            class="uppercase"
            v-for="({ coin, market }, index) in tokens"
            :key="index"
            @click="handleSelectToken(index)"
          >
            {{ coin }}/{{ market }}
          </span>
        </div>
      </div>
      <div :class="`${prefixCls}__detail--price`">
        <h3 class="text-green-410">8888</h3>
      </div>
      <div :class="`${prefixCls}__detail--percent`">
        <p>涨幅</p>
        <h4 class="text-green-410">+16.31%</h4>
      </div>
      <div :class="`${prefixCls}__detail--num`">
        <div>
          <ToolTips placement="top" trigger="hover">
            <h5>标记价格</h5>
            <template v-slot:content>
              <div class="text-left">
                标记价格同时考虑了现货指数价格及溢价指数，<br />强平价格和未实现盈亏的计算都依据此价格
              </div>
            </template>
          </ToolTips>
          <p>1.2230</p>
        </div>
        <div>
          <ToolTips placement="top" trigger="hover">
            <h5>指数价格</h5>
            <template v-slot:content>
              <div class="text-left">
                取多家主流交易所的现货价格并进行加权<br />平均，从而得出相对公允的参考价格
              </div>
            </template>
          </ToolTips>
          <p>1.2230</p>
        </div>
        <div>
          <ToolTips placement="top" trigger="hover">
            <h5>24H成交量</h5>
            <template v-slot:content><div>24H成交量</div></template>
          </ToolTips>
          <p>34,254</p>
        </div>
        <div>
          <ToolTips placement="top" trigger="hover">
            <h5>资金费率</h5>
            <template v-slot:content>
              <div class="text-left">
                用于减少现货市场和永续合约市场的价格偏差，每<br />小时将收取一次，由持仓用户之间互相支付和获取
              </div>
            </template>
          </ToolTips>
          <p>1.22%</p>
        </div>
      </div>
    </div>

    <!-- <a :class="`${prefixCls}__detail--help`">
      帮助中心
    </a> -->
  </div>
</template>
<script lang="ts">
  import { computed, defineComponent, onMounted, ref, watch, reactive } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import EventListener from '@/helper/eventListener';
  import { tradeConfStore, SelectedTokenInfo } from '@/store/modules/tradeConf';
  import { ws } from '@/utils/http/ws/socket';
  export default defineComponent({
    name: 'symbolDetail',
    setup() {
      const { prefixCls } = useDesign('symbol');
      const selectedTokenIndex = ref(0);
      const showTokenList = ref(false);
      const tokens = computed(() => {
        return tradeConfStore.getTokens;
      });
      const selectedTokenInfo = computed(() => {
        if (tokens.value.length) {
          return tokens.value[selectedTokenIndex.value];
        }
        return null;
      });

      watch(
        () => selectedTokenInfo.value,
        (val) => {
          if (val) {
            const { coin, market } = val;
            if (coin && market) {
              tradeConfStore.commitSelectedTokenInfo(val as SelectedTokenInfo);
              ws.subscribe(`${coin}16${market}`, { cmd: 16, c: coin, m: market }, (data) => {
                console.log('ws-data:', data);
              });
            }
          }
        }
      );

      function handleSelectToken(index: number) {
        selectedTokenIndex.value = index;
      }
      function handleShowTokenList() {
        showTokenList.value = !showTokenList.value;
      }
      onMounted(() => {
        EventListener.listen(document.body, 'click', function () {
          showTokenList.value = false;
        });
      });
      return {
        prefixCls,
        tokens,
        selectedTokenInfo,
        handleSelectToken,
        showTokenList,
        handleShowTokenList,
      };
    },
  });
</script>
