
  import { defineComponent, onMounted, reactive, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { positionApi } from '@/api/trade/position';
  import { bigNumberForamte } from '@/utils';
  enum ModalType {
    EARNEST_MONEY,
    CLOSE_POSITION,
  }
  export default defineComponent({
    name: 'userRecord',
    setup() {
      const { prefixCls } = useDesign('record');
      const open = reactive({
        earnestMoney: false,
        closePosition: false,
      });
      const margin = ref('');
      const marginRate = ref('');
      const openPrice = ref('');
      const closePrice = ref('');
      const volume = ref('');
      const unrealized = ref('');
      const pendding = ref(false);
      const lists = ref([]);
      function handleClickModal(
        type: ModalType,
        value: boolean,
        _margin?: number,
        _marginRate?: number,
        _openPrice?: number,
        _volume?: number,
        _unrealized?: number,
        _closePrice?: number
      ) {
        if (type === ModalType.EARNEST_MONEY) {
          open.earnestMoney = value;
          if (value) {
            margin.value = _margin ? bigNumberForamte(_margin, 2) : '';
            marginRate.value = _marginRate ? bigNumberForamte(_marginRate) : '';
            openPrice.value = _openPrice ? bigNumberForamte(_openPrice) : '';
            volume.value = _volume ? bigNumberForamte(_volume) : '';
            unrealized.value = _unrealized ? bigNumberForamte(_unrealized) : '';
            closePrice.value = _closePrice ? bigNumberForamte(_closePrice) : '';
            // unrealized.value
          }
        }
        if (type === ModalType.CLOSE_POSITION) open.closePosition = value;
      }
      onMounted(async () => {
        try {
          pendding.value = true;
          // const result = await positionApi();
          const result = [
            {
              name: 'BTC/USDC',
              type: 0,
              lerve: 3,
              volume: 1.2299990280231492e21,
              openPrice: 46274971976850960000,
              closePrice: 46273028023149040000,
              margin: 100971976850955780,
              marginRate: 2197197685095577.2,
              unrealized: 5252971976850956000,
              time: 1615779184,
            },
            {
              name: 'ETH/USDC',
              type: 0,
              lerve: 3,
              volume: 1.229999439831418e21,
              openPrice: 46274560168582050000,
              closePrice: 46273439831417950000,
              margin: 100560168582051970,
              marginRate: 2156016858205197.2,
              unrealized: 5252560168582052000,
              time: 1615779184,
            },
            {
              name: 'LTC/USDC',
              type: 0,
              lerve: 1,
              volume: 1.2299990100732226e21,
              openPrice: 46274989926777496000,
              closePrice: 46273010073222504000,
              margin: 100989926777494300,
              marginRate: 2198992677749430,
              unrealized: 5252989926777495000,
              time: 1615779184,
            },
          ];
          lists.value = result as any;
          pendding.value = false;
        } catch (error) {
          console.error(error);
        }
      });
      return {
        prefixCls,
        ModalType,
        handleClickModal,
        open,
        pendding,
        lists,
        bigNumberForamte,
        margin,
        marginRate,
        openPrice,
        volume,
        unrealized,
        closePrice,
      };
    },
  });
