<template>
  <div :class="`${prefixCls}__list`">
    <div :class="`${prefixCls}__list--item`" class="text-gray-460 mt-3">
      <span>成交时间</span>
      <span>合约</span>
      <span>交易类型</span>
      <span>成交数量</span>
      <span>成交均价</span>
      <span>手续费</span>
      <span>已实现盈亏</span>
      <span class="text-right">操作</span>
    </div>
    <div class="pt-5" v-if="pendding"><UiLoading class="big" /></div>
    <UiNoRecord v-if="!lists.length && !pendding" />
    <div
      v-else
      v-for="({ name, type, volume, realized, price, fee, time }, index) in lists"
      :key="index"
      :class="`${prefixCls}__list--item`"
      class="text-white"
    >
      <span>{{ dataFormate(time) }}</span>
      <span class="uppercase">{{ name }}</span>
      <span :class="type ? 'text-green-410' : 'text-red-410'">{{
        type ? '买入开多' : '卖出开空'
      }}</span>
      <span>{{ bigNumberForamte(volume) }}</span>
      <span>{{ bigNumberForamte(price, 2) }}</span>
      <span>{{ bigNumberForamte(fee) }}</span>
      <span>{{ bigNumberForamte(realized, 2) }}</span>
      <span class="text-right"><a href="" target="_blank">链上交易</a></span>
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, onMounted, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { getTradeHistoryApi } from '@/api/trade/tradeHistory';
  import { dataFormate } from '@/helper/utils';
  import { bigNumberForamte } from '@/utils';
  export default defineComponent({
    name: 'userClinch',
    setup() {
      const lists = ref([]);
      const pendding = ref(false);
      const { prefixCls } = useDesign('record');
      onMounted(async () => {
        pendding.value = true;
        const result = await getTradeHistoryApi();
        lists.value = result;
        pendding.value = false;
      });
      return { prefixCls, lists, dataFormate, bigNumberForamte, pendding };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-record';
  .@{prefix-cls} {
    &__list {
      &--item {
        margin-bottom: 13px;
        &:last-child {
          @apply mb-0;
        }
        @apply flex flex-row text-left text-xs justify-between;
        span {
          &:first-child {
            flex-grow: 1.8;
          }
          line-height: 14px;
          @apply flex-1;
          &:last-child {
            flex-shrink: 0.5;
            @aplpy text-right;
          }
        }
      }
    }
  }
</style>
