
  import { defineComponent, ref, computed, watch, onMounted } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import SlipPoint from './Slippage.vue';
  import SliderBar from './SliderBar.vue';
  import { loadingBarStore, LoadingStatus } from '@/store/modules/loadingBar';
  import ClearingHouse from '@/helper/clearingHouse';
  import Web3Helper from '@/helper/web3';
  import { accountStore } from '@/store/modules/account';
  import { modalStore } from '@/store/modules/modal';
  import { Web3Provider } from '@ethersproject/providers';
  import config from '@/config';
  import BigNumber from 'bignumber.js';
  import { scale } from '@/utils';
  import { BigNumber as BN } from '@ethersproject/bignumber';
  import { notifyStore } from '@/store/modules/notifications';
  import { formateInput } from '@/utils';
  import { positionApi } from '@/api/trade/position';
  import { AmmContract } from '@/helper/amm';
  import {
    ValidationError,
    validateNumberInput,
    PositionValidation,
    InputType,
    Type,
    positionType,
  } from '@/utils/validation';
  import { formatEther, parseEther, parseUnits } from '@ethersproject/units';

  export default defineComponent({
    name: 'trading',
    components: { SlipPoint, SliderBar },
    setup() {
      const tabIndex = ref<positionType>(positionType.BUY);
      const slipPoint = ref<number>(0.5);
      const leverage = ref(4);
      // 开平仓费率
      const rate = ref(0.01);
      // 入场价格
      const price = ref(52907.09);
      // quoteAssetAmount   开仓本金
      const amount = ref('');
      /**
       * 可获得token数量
       * volume 计算公示   volume = 开仓本金 * 杠杆 / 价格
       */
      const volume = ref('');
      const { prefixCls } = useDesign('trading');
      const handleChangeTab = (index: number) => {
        tabIndex.value = index;
      };
      // 滑点
      const updateSlippage = (val: number) => {
        slipPoint.value = val;
      };
      // 杠杆倍数
      const updateRatio = (val: number) => {
        leverage.value = val;
      };
      // 开仓均价
      const openPrice = computed(() => {
        const num = new BigNumber(amount.value)
          .times(new BigNumber(leverage.value))
          .div(new BigNumber(volume.value));
        if (num.isNaN()) return '0';
        return num.toNumber().toFixed(2);
      });
      const unlock = async () => {
        loadingBarStore.setLoading(LoadingStatus.POSITION);
        try {
          const provider = await accountStore.getProvider();
          const result = await Web3Helper.approve(provider as Web3Provider);
          console.log('succ');
        } catch (error) {
          console.log('fail');
        } finally {
          loadingBarStore.setLoading(LoadingStatus.NONE);
        }
      };
      const activeInput = computed(() => {
        return amount.value || volume.value;
      });
      // 交易手续费
      const tradeFee = computed(() => {
        const num = new BigNumber(amount.value).times(new BigNumber(rate.value));
        if (num.isNaN()) return 0;
        return num.toNumber().toFixed(2);
      });
      // 总花费
      const total = computed(() => {
        const num = new BigNumber(amount.value)
          .times(new BigNumber(rate.value))
          .plus(new BigNumber(amount.value));
        if (num.isNaN()) return 0;
        return num.toNumber().toFixed(2);
      });
      const userBalance = computed(() => {
        const { balances, address } = accountStore.getAccountState;
        if (!address || !balances[address]) return 0;

        return scale(new BigNumber(balances[address].toString()), -6).toNumber();
      });
      const account = computed(() => {
        const { connector, address } = accountStore.getAccountState;
        if (!connector || !connector.id || !address) {
          return '';
        }
        return address;
      });
      const type = computed(() => {
        if (!account.value) {
          return Type.Connect;
        }
        if (!isUnlocked.value) {
          return Type.Unlock;
        } else {
          return Type.Trade;
        }
      });
      const transactionPending = computed(() => {
        return loadingBarStore.getLoadingState === LoadingStatus.POSITION;
      });
      const disabled = computed(() => {
        if (type.value !== Type.Connect) return validation.value !== PositionValidation.NONE;
        return false;
        // if (type.value === Type.Connect) {
        //   const { connector, address } = accountStore.getAccountState;
        //   return !!connector && !!connector.id && !address;
        // } else {
        //   return validation.value !== PositionValidation.NONE;
        // }
      });
      const text = computed(() => {
        return actionText.value;
        // if (loading.value) {
        //   return actionText.value;
        // }
        // if (disabled.value) {
        //   return actionText.value;
        // } else {
        //   return actionText.value;
        // }
      });
      const actionText = computed(() => {
        if (type.value === Type.Connect) {
          return 'Connect Wallet';
        }
        if (type.value === Type.Unlock) {
          return 'Unlock';
        }
        if (type.value === Type.Trade) {
          return tabIndex.value === positionType.BUY ? '买入开多（看涨）' : '卖出开空（看跌）';
        }
        return '';
      });
      const errorText = computed(() => {
        if (validation.value === PositionValidation.WRONG_NETWORK) {
          return 'Wrong network';
        }
        // @note 未输入值不提示
        // if (validation.value === PositionValidation.EMPTY_INPUT) {
        //   return '请输入数值';
        // }
        if (validation.value === PositionValidation.INVALID_INPUT) {
          return '数值输入错误';
        }
        if (validation.value === PositionValidation.TOO_MIN) {
          return '单笔最小开仓数为10USDT';
        }
        if (validation.value === PositionValidation.TOO_MAX) {
          return '单笔最大开仓保证金1000000USDT';
        }
        if (validation.value === PositionValidation.INSUFFICIENT_BALANCE) {
          return '资金不足';
        }
        return '';
      });
      const loading = computed(() => {
        if (type.value === Type.Connect) {
          const { connector, address } = accountStore.getAccountState;
          return !!connector && !!connector.id && !address;
        } else {
          return false;
        }
      });
      const isUnlocked = computed(() => {
        const { allowances, metaData, address } = accountStore.getAccountState;

        const { clearingHouse, assets } = config.address;
        if (
          parseFloat(volume.value.toString()) <= 0 ||
          isNaN(parseFloat(volume.value.toString()))
        ) {
          return true;
        }
        if (!allowances[clearingHouse]) {
          return true;
        }
        const allowance = allowances[clearingHouse][assets];

        if (!allowance) {
          return true;
        }
        const decimals = metaData[address].decimals;

        if (!decimals) {
          return true;
        }
        const allowanceNumber = new BigNumber(allowance.toString());
        const allowanceRaw = scale(allowanceNumber, -decimals);

        return allowanceRaw.gte(volume.value);
      });
      const validation = computed(() => {
        // Invalid input
        const { balances, chainId, metaData, address } = accountStore.getAccountState;
        const { clearingHouse, assets } = config.address;
        const inputError = validateNumberInput(activeInput.value.toString());
        // No account
        if (!account.value) {
          return PositionValidation.NO_ACCOUNT;
        }
        // Wrong network
        if (config.chainId !== chainId) {
          return PositionValidation.WRONG_NETWORK;
        }
        if (inputError === ValidationError.EMPTY) {
          return PositionValidation.EMPTY_INPUT;
        }

        if (new BigNumber(amount.value).lt(new BigNumber(10))) {
          return PositionValidation.TOO_MIN;
        }
        if (new BigNumber(amount.value).gt(new BigNumber(1000000))) {
          return PositionValidation.TOO_MAX;
        }
        if (inputError !== ValidationError.NONE) {
          return PositionValidation.INVALID_INPUT;
        }

        // Insufficient balance
        const assetMetadata = metaData[address];
        if (!assetMetadata) {
          return PositionValidation.INSUFFICIENT_BALANCE;
        }
        const assetDecimals = assetMetadata.decimals;
        const assetBalanceRaw = new BigNumber(balances[address].toString());

        const assetBalance = scale(assetBalanceRaw, -assetDecimals);
        const assetAmount = new BigNumber(activeInput.value);

        if (!assetBalance || assetAmount.gt(assetBalance)) {
          return PositionValidation.INSUFFICIENT_BALANCE;
        }
        return PositionValidation.NONE;
      });
      async function handleChange(e: any, type: InputType) {
        // 格式化输入框内容
        const value = formateInput(e.target.value);
        e.target.value = value;

        if (!value || isNaN(parseFloat(value))) {
          amount.value = '';
          volume.value = '';
          return;
        }
        if (type === InputType.AMOUNT) {
          amount.value = value;
          const provider = (await accountStore.getProvider()) as Web3Provider;
          const result = await AmmContract(provider, [
            '0x8E54f5215F1bD40D64C0d6afdBe71B6594900BAF',
            'getInputPrice',
            [BN.from(1), { d: parseEther(value) }],
          ]);
          volume.value = formatEther(result.d);

          return;
        }
        if (type === InputType.VOLUME) {
          volume.value = value;
          // const num = new BigNumber(value)
          //   .div(new BigNumber(leverage.value))
          //   .times(new BigNumber(price.value));
          // amount.value = num.toNumber().toFixed(2);
          // return;
          const provider = (await accountStore.getProvider()) as Web3Provider;
          const result = await AmmContract(provider, [
            '0x8E54f5215F1bD40D64C0d6afdBe71B6594900BAF',
            'getOutputPrice',
            [BN.from(1), { d: parseEther(value) }],
          ]);
          amount.value = formatEther(result.d);

          return;
        }
      }
      async function handleClick() {
        if (type.value === Type.Connect) {
          // @todo 链接metamask
          connect();
        } else if (type.value === Type.Unlock) {
          // 授权解锁
          unlock();
        } else {
          // console.log(BN.from(2).pow(2));
          console.log(validation.value);
          position();
          // @todo 开仓
        }
      }
      function connect() {
        modalStore.setConnectorModalState(true);
      }
      async function position() {
        if (
          validation.value === PositionValidation.EMPTY_INPUT ||
          validation.value === PositionValidation.INVALID_INPUT
        ) {
          return '';
        }
        try {
          loadingBarStore.setLoading(LoadingStatus.POSITION);
          const provider = (await accountStore.getProvider()) as Web3Provider;
          const tx = await ClearingHouse.openPosition(
            provider,
            '0x8E54f5215F1bD40D64C0d6afdBe71B6594900BAF',
            BN.from(tabIndex.value),
            parseUnits(amount.value, 6),
            // BN.from(new BigNumber(amount.value).multipliedBy(new BigNumber(10).pow(6)).toString()),
            BN.from(leverage.value),
            parseUnits(volume.value, 18)
            // BN.from(new BigNumber(volume.value).multipliedBy(new BigNumber(10).pow(18)).toString())
          );
          console.log((await provider.estimateGas(tx)).toNumber());

          const transactionReceipt = await provider.waitForTransaction(tx.hash, 1);
          volume.value = '';
          amount.value = '';
          console.log(transactionReceipt);
        } catch (error) {
          if (error.data && error.data.message) {
            notifyStore.commitNotify([error.data.message, 'red']);
          } else {
            console.error(error);
          }
        } finally {
          loadingBarStore.setLoading(LoadingStatus.NONE);
        }
      }
      const address = computed(() => {
        return accountStore.getAccountState.address;
      });
      watch(address, (val) => {
        accountStore.fetchAssets(val);
      });
      watch(leverage, (val) => {
        const value = volume.value;
        const num = new BigNumber(value)
          .div(new BigNumber(leverage.value))
          .times(new BigNumber(price.value));
        if (num.isNaN()) return;
        amount.value = num.toNumber().toFixed(2);
      });
      // onMounted(async () => {
      //   const provider = (await accountStore.getProvider()) as Web3Provider;
      //   const tx = await Amm.getReserve(provider, '0x8E54f5215F1bD40D64C0d6afdBe71B6594900BAF');
      //   console.log(formatEther(tx[0].d));
      //   console.log(formatEther(tx[1].d));
      // });
      return {
        prefixCls,
        tabIndex,
        handleChangeTab,
        updateSlippage,
        updateRatio,
        handleClick,
        disabled,
        loading,
        text,
        validation,
        errorText,
        volume,
        amount,
        handleChange,
        positionType,
        userBalance,
        InputType,
        price,
        tradeFee,
        total,
        transactionPending,
        openPrice,
      };
    },
  });
