<template>
  <div :class="`${prefixCls}__list`">
    <div :class="`${prefixCls}__list--item`" class="text-gray-460 mt-3">
      <span>成交时间</span>
      <span>合约</span>
      <span>资金费用</span>
      <span>资金费率</span>
    </div>
    <div class="pt-5" v-if="pendding"><UiLoading class="big" /></div>
    <UiNoRecord v-if="!lists.length && !pendding" />
    <div
      v-else
      v-for="({ time, name, amount, rate }, index) in lists"
      :key="index"
      :class="`${prefixCls}__list--item`"
      class="text-white"
    >
      <span>{{ dataFormate(time) }}</span>
      <span class="uppercase">{{ name }}</span>
      <span>{{ bigNumberForamte(amount, 2) }}</span>
      <span>{{ bigNumberForamte(rate, 2) }}</span>
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, onMounted, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { getFundingPaymentApi } from '@/api/trade/fundingPayment';
  import { dataFormate } from '@/helper/utils';
  import { scale, bigNumberForamte } from '@/utils';
  import BigNumber from 'bignumber.js';
  export default defineComponent({
    name: 'fee',
    setup() {
      const { prefixCls } = useDesign('record');
      const lists = ref([]);
      const pendding = ref(false);
      onMounted(async () => {
        pendding.value = true;
        const result = await getFundingPaymentApi();
        lists.value = result;
        pendding.value = false;
      });
      return { prefixCls, lists, dataFormate, bigNumberForamte, pendding };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-record';
  .@{prefix-cls} {
    &__list {
      &--item {
        margin-bottom: 13px;
        &:last-child {
          @apply mb-0;
        }
        @apply flex flex-row text-left text-xs;
        span {
          line-height: 14px;
          @apply flex-1;
          &:nth-child(1) {
            flex-grow: 4;
          }

          &:nth-child(2),
          &:nth-child(3) {
            flex-grow: 3;
          }
          &:last-child {
            flex-grow: 1;
            @apply text-right;
          }
        }
      }
    }
  }
</style>
