<template>
  <div :class="`${prefixCls}__list`">
    <div :class="`${prefixCls}__list--item`" class="text-gray-460 mt-3">
      <span class="col">资产</span>
      <span class="col">方向</span>
      <span class="col">杠杆</span>
      <span class="col">数量</span>
      <span class="col">开仓均价</span>
      <div class="col">
        <ToolTips class="inline-block" placement="top" trigger="hover">
          <span class="borderd">强平价格</span>
          <template v-slot:content
            ><div class="text-left"
              >当仓位保证金率=维持保证金率+强平手续费率时<br />的价格，若市场触发此价格，将启动清仓</div
            ></template
          >
        </ToolTips>
      </div>
      <div class="col">
        <ToolTips class="inline-block" placement="top" trigger="hover">
          <span class="borderd">保证金</span>
          <template v-slot:content><div>用户当前所有持仓的抵押金数量</div></template>
        </ToolTips>
      </div>
      <div class="col">
        <ToolTips class="inline-block" placement="top" trigger="hover">
          <span class="borderd">保证金率</span>
          <template v-slot:content
            ><div class="text-left"
              >保证金率=（保证金额+未实现盈亏）/开仓价值。<br />保证金率越低，风险越高。保证金率低于维持<br />保证金率时，仓位将会被系统强制平仓</div
            ></template
          >
        </ToolTips>
      </div>
      <div class="col">
        <ToolTips class="inline-block" placement="top" trigger="hover">
          <span class="borderd">未实现盈亏</span>
          <template v-slot:content
            ><div class="text-left"
              >用户未平仓仓位的浮动盈亏，<br />以标记价格为依据计算</div
            ></template
          >
        </ToolTips>
      </div>
      <span class="text-right col">操作</span>
    </div>
    <div class="pt-5" v-if="pendding"><UiLoading class="big" /></div>
    <UiNoRecord v-if="!lists.length && !pendding" />
    <div
      v-for="({ name, type, lerve, volume, openPrice, closePrice, margin, marginRate, unrealized },
      index) in lists"
      :key="index"
      :class="`${prefixCls}__list--item`"
      class="text-white"
    >
      <span class="col uppercase">{{ name }}</span>
      <span class="text-green-410 col">{{ type === 0 ? '多' : '空' }}</span>
      <span class="col">{{ lerve }}X</span>
      <span class="col">{{ bigNumberForamte(volume) }}</span>
      <span class="col">{{ bigNumberForamte(openPrice, 2) }}</span>
      <span class="col">{{ bigNumberForamte(closePrice, 2) }}</span>
      <span class="col">{{ bigNumberForamte(margin, 2) }}</span>
      <span class="col">{{ bigNumberForamte(marginRate) }}%</span>
      <span class="col">{{ bigNumberForamte(unrealized, 2) }}</span>
      <span class="text-right col"
        ><a
          href="javascript:;"
          @click="
            handleClickModal(
              ModalType.EARNEST_MONEY,
              true,
              margin,
              marginRate,
              openPrice,
              volume,
              unrealized,
              closePrice
            )
          "
          class="mr-8"
          >保证金管理</a
        >
        <a href="javascript:;" @click="handleClickModal(ModalType.CLOSE_POSITION, true)"
          >强平</a
        ></span
      >
    </div>
    <Teleport to="#modals">
      <ModalEarnestMoney
        :margin="margin"
        :marginRate="marginRate"
        :openPrice="openPrice"
        :volume="volume"
        :address="'0x8E54f5215F1bD40D64C0d6afdBe71B6594900BAF'"
        :open="open.earnestMoney"
        @close="handleClickModal(ModalType.EARNEST_MONEY, false)"
      />
    </Teleport>
    <Teleport to="#modals">
      <ModalClosePosition
        :open="open.closePosition"
        :closePrice="closePrice"
        :margin="margin"
        :unrealized="unrealized"
        :address="'0x8E54f5215F1bD40D64C0d6afdBe71B6594900BAF'"
        @close="handleClickModal(ModalType.CLOSE_POSITION, false)"
      />
    </Teleport>
  </div>
</template>
<script lang="ts">
  import { defineComponent, onMounted, reactive, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { positionApi } from '@/api/trade/position';
  import { bigNumberForamte } from '@/utils';
  enum ModalType {
    EARNEST_MONEY,
    CLOSE_POSITION,
  }
  export default defineComponent({
    name: 'userRecord',
    setup() {
      const { prefixCls } = useDesign('record');
      const open = reactive({
        earnestMoney: false,
        closePosition: false,
      });
      const margin = ref('');
      const marginRate = ref('');
      const openPrice = ref('');
      const closePrice = ref('');
      const volume = ref('');
      const unrealized = ref('');
      const pendding = ref(false);
      const lists = ref([]);
      function handleClickModal(
        type: ModalType,
        value: boolean,
        _margin?: number,
        _marginRate?: number,
        _openPrice?: number,
        _volume?: number,
        _unrealized?: number,
        _closePrice?: number
      ) {
        if (type === ModalType.EARNEST_MONEY) {
          open.earnestMoney = value;
          if (value) {
            margin.value = _margin ? bigNumberForamte(_margin, 2) : '';
            marginRate.value = _marginRate ? bigNumberForamte(_marginRate) : '';
            openPrice.value = _openPrice ? bigNumberForamte(_openPrice) : '';
            volume.value = _volume ? bigNumberForamte(_volume) : '';
            unrealized.value = _unrealized ? bigNumberForamte(_unrealized) : '';
            closePrice.value = _closePrice ? bigNumberForamte(_closePrice) : '';
            // unrealized.value
          }
        }
        if (type === ModalType.CLOSE_POSITION) open.closePosition = value;
      }
      onMounted(async () => {
        try {
          pendding.value = true;
          // const result = await positionApi();
          const result = [
            {
              name: 'BTC/USDC',
              type: 0,
              lerve: 3,
              volume: 1.2299990280231492e21,
              openPrice: 46274971976850960000,
              closePrice: 46273028023149040000,
              margin: 100971976850955780,
              marginRate: 2197197685095577.2,
              unrealized: 5252971976850956000,
              time: 1615779184,
            },
            {
              name: 'ETH/USDC',
              type: 0,
              lerve: 3,
              volume: 1.229999439831418e21,
              openPrice: 46274560168582050000,
              closePrice: 46273439831417950000,
              margin: 100560168582051970,
              marginRate: 2156016858205197.2,
              unrealized: 5252560168582052000,
              time: 1615779184,
            },
            {
              name: 'LTC/USDC',
              type: 0,
              lerve: 1,
              volume: 1.2299990100732226e21,
              openPrice: 46274989926777496000,
              closePrice: 46273010073222504000,
              margin: 100989926777494300,
              marginRate: 2198992677749430,
              unrealized: 5252989926777495000,
              time: 1615779184,
            },
          ];
          lists.value = result as any;
          pendding.value = false;
        } catch (error) {
          console.error(error);
        }
      });
      return {
        prefixCls,
        ModalType,
        handleClickModal,
        open,
        pendding,
        lists,
        bigNumberForamte,
        margin,
        marginRate,
        openPrice,
        volume,
        unrealized,
        closePrice,
      };
    },
  });
</script>
