  @prefix-cls:~ '@{namespace}-record';
  .@{prefix-cls} {
    &__list {
      &--item {
        margin-bottom: 13px;
        &:last-child {
          @apply mb-0;
        }
        @apply flex flex-row text-left text-xs;
        .col {
          line-height: 14px;
          @apply flex-1;
          &:last-child {
            flex-grow: 1.6;
            @aplpy text-right;
          }
          .borderd {
            border-bottom: 1px dashed rgba(247, 247, 247, 0.1);
            padding-bottom: 4px;
          }
        }
      }
